<template>
  <div class="register">
    <mu-appbar title='' class='header'>
      <router-link to='/' slot='left'>
        <mu-icon-button icon='keyboard_arrow_left' style='color: #fff'/>
      </router-link>
    </mu-appbar>
    <div class="page-part">
      <mu-text-field label="" hintText="" v-model="userinfo.name"></mu-text-field></br>
      <mu-text-field label="" hintText="" type="password" v-model="userinfo.password"></mu-text-field></br>
      <mu-text-field label="" hintText="" type="password" v-model="retypePwd"></mu-text-field></br>
      <div class="address">
        <mu-select-field v-model='country' label='' @change='countryChange'>
          <mu-menu-item v-for='text, index in countryList' :key='index' :value='index' :title='text' />
        </mu-select-field>
        <mu-select-field v-model='province' label=''  @change='provinceChange'>
          <mu-menu-item v-for='item,index in provinceList' :key='index' :value='index' :title='item.name'/>
        </mu-select-field>
      </div>
      <mu-text-field label="" hintText="" v-model="userinfo.tel"></mu-text-field></br>
      <mu-text-field label="" hintText="" type="email" v-model="userinfo.email"></mu-text-field></br>
      <mu-text-field label="" hintText="" v-model="userinfo.company"></mu-text-field></br>
      <mu-select-field v-model='userinfo.userType' label='' >
          <mu-menu-item value='producter' title=''/>
          <mu-menu-item value='user' title=''/>
      </mu-select-field>
    </div>
    <mu-raised-button label="" class="demo-raised-button" primary  @click="userRegister"/><br/><br/><br/>
    <mu-dialog :open="alertDialog" v-if="alertDialog" :showHide="alertDialog" title="">
      <p>{{ alertText }}</p>
    <mu-flat-button label="" slot="actions" primary @click="closeAlert"/>
  </mu-dialog>
  </div>
</template>

<script>
import { regions } from '@/utils/region'
import { checkUserName, checkEmail, checkPhoneNum } from '@/utils/validate'
export default {
  name: 'register',
  data () {
    return {
      userinfo: {
        name: '',
        password: '',
        country: '',
        province: '',
        company: '',
        tel: '',
        email: '',
        userType: 'producter'
      },
      retypePwd: '',
      country: 0,
      countryList: [''],
      province: 0,
      alertDialog: false,
      alertText: ''
    }
  },
  computed: {
    provinceList() {
      return regions;
    }
  },
  methods: {
    checkUserInput() {
      if(!checkUserName(this.userinfo.name)) {
        this.alertText = '';
        return;
      }
      if(this.userinfo.password.length < 6) {
        this.alertText = '6';
        return
      }
      if(this.userinfo.password != this.retypePwd) {
        this.alertText = ',';
        return;
      }
      if(!checkPhoneNum(this.userinfo.tel)) {
        this.alertText = '';
        return;
      }
      if(!checkEmail(this.userinfo.email)) {
        this.alertText = '';
        return;
      }
      return true;
    },
    userRegister() {
      this.userinfo.country = this.countryList[this.country];
      this.userinfo.province = regions[this.province].name;
      
      if(!this.checkUserInput()) {
        this.alertDialog = true;
        return;
      }
      
      console.log(this.userinfo);
      this.$store.dispatch('userRegister', this.userinfo).then(() => {
        console.log('');
        this.$router.push({ path: '/' });
      }).catch(err => {
        console.log('');
      });
    },
    countryChange() {
      this.userinfo.country = this.countryList[this.country];
      console.log(this.userinfo.country);
    },
    provinceChange(val) {
      this.userinfo.province = regions[val].name;
      console.log(this.userinfo.province);
    },
    closeAlert() {
      this.alertDialog = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.regTop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.address .mu-select-field {
  padding-left: 0px;
  display: inline-block;
  width: 126px;
}
.mu-text-field {
  height: 40px;
}
</style>



