<template>
  <div class="login">
    <h3></h3>
    <mu-text-field label="" hintText="" v-model="userinfo.name"></mu-text-field></br>
    <mu-text-field label="" hintText="" type="password" v-model="userinfo.password"></mu-text-field></br>
    <mu-select-field v-model='userinfo.userType' label='' >
      <mu-menu-item value='producter' title=''/>
      <mu-menu-item value='user' title=''/>
    </mu-select-field><br/>
    <mu-raised-button label="" class="demo-raised-button" primary  @click="userlogin"/>
    <div style="padding-top: 200px" class="link">
      <router-link to='/register'></router-link>
      <router-link to='getpwd'></router-link>
    </div>
    <mu-dialog :open="alertDialog" v-if="alertDialog" :showHide="alertDialog" title="">
      <p>{{ alertText }}</p>
      <mu-flat-button label="" slot="actions" primary @click="closeAlert"/>
    </mu-dialog>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      userinfo: {
        userType: 'producter',
        name: '',
        password: ''
      },
      alertDialog: false,
      alertText: ''
    }
  },
  computed: {
    /*userinfo() {
      return {
        name: this.$store.state.user.name
      }
    }*/
  },
  methods: {
    userlogin() {
      console.log('' + this.userinfo.name);
      console.log('' + this.userinfo.password);
      if(this.userinfo.password.length <= 0) {
        this.alertDialog = true;
        this.alertText = '';
        return
      }
      this.$store.dispatch('userLogin', this.userinfo).then(() => {
        console.log('');
        this.$router.push({ path: '/monitor' });
      }).catch(err => {
        console.log('');
      });
    },
    closeAlert() {
      this.alertDialog = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: blue;
}
*/
.link {
  color: lightblue;
}
</style>



