import axios from '@/utils/http'

//
export function register(userinfo) {
	console.log('register');
	console.log(userinfo);
	const data = userinfo;
	console.log(data);
	return axios({
		url: '/v1/register',
		method: 'post',
		data
	})
}

//
export function login(userType, name, password) {
	console.log('api+login');
	const data = {
		userType,
		name,
		password
	};
	return axios({
		url: '/v1/login',
		method: 'post',
		data
	});
}

//
export function getUserinfo() {
	return axios({
		url: '/v1/user_information',
		method: 'get'
	})

}

//
export function logout() {
	return axios({
		url: '/v1/logout',
		method: 'post'
	});
}

/*  */
//
export function addDevice(data) {
	return axios({
		url: '/v1/register_devices',
		method: 'post',
		data
	})
}

//
export function getDevices(token) {
	return axios({
		url: '/v1/devices',
		method: 'get',
		params: { token }
	});
}
//
export function shareDevice(data) {
	return axios({
		url: '/v1/sharedevice',
		method: 'post',
		data
	})
}

/*  */
export function getFriends() {
	return axios({
		url: '/v1/friendlist',
		method: 'get'
	})
}

//
export function addFriend(data) {
	return axios({
		url: '/v1/addfriend',
		method: 'post',
		data
	})
}

//
export function getUnreceivedList() {
	return axios({
		url: '/v1/unreceived_friendlist',
		method: 'get'
	})
}

//
export function getUnconfirmedList() {
	return axios({
		url: '/v1/unconfirmed_friendlist',
		method: 'get'
	})
}
//
export function receiveFriend(data) {
	return axios({
		url: '/v1/receivefriend',
		method: 'post',
		data
	})
}


